<template>
    <div class="quillEditor">
        <div v-if="vmObj" v-quillEditorArea="vmObj"></div>
    </div>
</template>

<script>
/* eslint-disable */
import Vue from 'vue';
export default {
    name: 'quillEditor',
    props: {
        value: '',
        config: {
            type: [Object],
            default: function () {
                return {}
            }
        },
        height: {
            type: Number,
            default: 360
        }
    },
    model: {
        prop: 'value',
        event: 'input'
    },
    computed:{
        currentValue: {
            get:function() {
                return this.value;
            },
            set:function(val) {
                this.$emit('input', val);
            }
        }
    },
    data() {
        return {
            vmObj: this,
            loaded: false
        }
    },
    directives: {
        quillEditorArea: {
            inserted: function (el, binding, vnode) {
                let vm = binding.value;
                vm.dataBase.load('quill',() => {
                    vm.loaded = true;
                    var defaultConfig = {
                        theme: 'snow',
                        modules: {
                            toolbar: [
                                ['bold', 'italic'],
                                [{ 'list': 'ordered'}, { 'list': 'bullet' }],
                                [{ size: ["small", false, "large", "huge"] }], // 
                                [{ color: [] }, { background: [] }], // 
                                [{ font: [] }], // 
                                [{ align: [] }], // 
                                ["clean"], // 
                                ['image']
                            ],
                            history: {
                                delay: 1000,
                                maxStack: 50,
                                userOnly: false
                            },
                            imageDrop: true,
                            imageResize: {
                                displayStyles: {
                                    backgroundColor: 'black',
                                    border: 'none',
                                    color: 'white'
                                },
                                modules: [ 'Resize', 'DisplaySize', 'Toolbar' ]
                            }
                        }
                    }
                    let config = Object.assign({}, defaultConfig, vm.config);
                    var quill = new window.Quill(el, config);
                    quill.container.style.height = `${vm.height}px`
                    quill.pasteHTML(vm.currentValue);
                    quill.on('text-change', (delta, oldDelta, source) => {
                        vm.$emit('input',vm.$el.children[1].children[0].innerHTML)
                    })
                    vm.$emit('on-init', quill);
                });
            }
        }
    },
}
</script>

<style>
.ql-editor {
    height: 350px;
}
</style>