<template>
    <div class="container">
        <mBreadcrumb separator=">"></mBreadcrumb>
        <div class="gc-container">
            <div class="gc-container__title"></div>
            <div>2</div>
            <sDivider></sDivider>
            <div>{{authToken}}</div>
            <sButton type="primary" @click="setCookie">auth_token</sButton>
            <sButton type="primary" :disabled="!disabled" @click="getCookie">auth_token</sButton>
            <sButton type="primary" :disabled="!disabled" @click="delCookie">auth_token</sButton>
        </div>
    </div>
</template>

<script>
/* eslint-disable */
import mBreadcrumb from '@base/Breadcrumb'
import cache from '@/utils/cache'
import { mockToken } from '@/utils'

export default {
    data () {
        return {
            authToken: '',
            disabled: false
        }
    },
    methods: {
        setCookie() {
            this.disabled = true
            cache.setCookie('auth_token', mockToken(), 1)
        },
        getCookie() {
            this.authToken = cache.getCookie('auth_token')
        },
        delCookie() {
            this.authToken = cache.delCookie('auth_token')
        }
    },
    components: { mBreadcrumb }
}
</script>