<template>
    <div v-if="currentValue">
        <sModal class="gc-dia-main" width="1000" v-model="currentValue" transfer :closable="false" :mask-closable="false">
            <div slot="header" class="gc-dia-title"></div>
            <div class="gc-dia-body" style="position:relative;min-height:300px;">
                <sUpload
                    ref="upload"
                    :show-upload-list="false"
                    :before-upload="handleBeforeUpload"
                    :format="['jpg','jpeg','png']"
                    :max-size="200"
                    :action="`${dataBase.apihost}/admin/file/upload`"
                    :on-format-error="handleFormatError"
                    :on-exceeded-size="handleMaxSize"
                    :on-success="handleSuccess"
                    style="display: inline-block;width:92px;">
                    <sButton type="info"></sButton>
                </sUpload>
                <div class="wrapper">
                    <VueCropper ref="cropper" 
                        :img="myCropper.img" 
                        :outputSize="myCropper.size" 
                        :outputType="myCropper.outputType" 
                        :info="myCropper.info" 
                        :canScale="myCropper.canScale" 
                        :autoCrop="myCropper.autoCrop" 
                        :autoCropWidth="myCropper.width" 
                        :autoCropHeight="myCropper.height">
                    </VueCropper>
                </div>
            </div>
            <div slot="footer" class="gc-dia-foot">
                <sButton size="large" class="gc-btn-text gc-btn-no" type="text" @click="handleCancel"></sButton>
                <sButton size="large" class="gc-btn-text gc-btn-yes" type="text" @click="handleFinish(fileType)"></sButton>
            </div>
        </sModal>
    </div>
</template>

<script>
/* eslint-disable */
import { VueCropper } from 'vue-cropper';

export default {
    props: {
        size: Number,
        option: Object,
        value: {
            type: Boolean,
            default: false
        },
        source: {
            type: String,
            default: ''
        }
    },
    model: {
        prop: 'value',
        event: 'change'
    },
    components: { VueCropper },
    data() {
        return {
            fileName: '',
            fileType: 'Blob',
            isShow: '',
            headers: {
                'Content-Type': 'multipart/form-data',
                'Authorization': 'Bearer 12be9d0b-98d3-4d4b-a075-2a5d0eda10f3'
            },
            myCropper: {
                img: '',
                info: true,
                size: 1,
                outputType: 'png',
                canScale: false,
                autoCrop: true,             //  
                width: 150,
                height: 150
            }
        }
    },
    computed:{
        currentValue: {
            get() {
                return this.value
            },
            set(val) {
                this.$emit('change', val)
            }
        }
    },
    methods: {
        handleBeforeUpload(file) {
            //  FileReader 
            let reader = new FileReader();
            let imgUrlBase64;
            // readAsDataURL  Blob  File 
            // readyState  DONEloadend  result URL base64  URL 
            if (file) {
                imgUrlBase64 = reader.readAsDataURL(file);     // base64 
                reader.onload = (e) => {
                    this.myCropper.img = reader.result;
                }
                const fileName = `${(file || {}).name}`;
            }
        },
        handleSuccess(res, file) {
            console.log(res, file)
        },
        handleFormatError(file) {
            this.$Notice.warning({
                title: '',
                desc: `${file.name}, jpgpng`
            });
        },
        handleMaxSize (file) {
            this.$Notice.warning({
                title: '',
                desc: `${file.name }, 200KB`
            });
        },
        handleFinish(type) {
            let sData = '';
            let { img } = this.myCropper;
            if(img && img !== '') {
                // 
                this.$refs.cropper.getCropData((data) => {
                    const dataB64 = data;   //base64 
                    if (type === 'Blob') {
                        this.getCropBlob(data, (blobData) => {
                            sData = blobData;
                        });
                    } else {
                        sData = data;
                    }
                    this.myCropper.img = '';
                    this.currentValue = false;
                    this.$emit('on-ok', {url: dataB64});
                    // this.dataBase.log('64', sData, dataB64);
                });
            }else {
                this.$Message.error("");
            }
        },
        handleCancel() {
            const params = {url: ''};
            this.myCropper.img = '';
            this.currentValue = false;
            this.$emit('on-cancel', params);
        },
        getCropBlob(data, cb) {
            var arr = data.split(',')
            var mime = arr[0].match(/:(.*?);/)[1]
            var bstr = atob(arr[1])
            var n = bstr.length
            var u8arr = new Uint8Array(n)
            while (n--) {
                u8arr[n] = bstr.charCodeAt(n)
            }
            cb(
                new Blob([u8arr], {
                    type: mime
                })
            )
        },
        handleUpload() {
            const headers = {
                'Content-Type': 'multipart/form-data',
                'Authorization': 'Bearer 12be9d0b-98d3-4d4b-a075-2a5d0eda10f3'
            };
            const parmas = {
                file: this.fileName
            };
            this.$http({
                method: 'post',
                headers: headers,
                url: `${this.dataBase.apihost}/admin/file/upload`,
                data: parmas
            }).then(res => {
                console.log(res)
            });
        }
    }
}
</script>

<style lang="less">
.wrapper {
    position: relative;
    margin-top: 15px;
    width: 100%;
    height: 300px;
}
</style>
